@import url(https://fonts.googleapis.com/css?family=Roboto&display=swap);
body {
  background-color: #282c34;
  margin: 0;
  font-family: "Roboto", sans-serif;
}

.header {
  color: whitesmoke;
  background-color: #333842;
  padding: 20px;
}

.title {
  margin-left: 10px;
}

.menu {
  display: flex;
  justify-content: center;
}

.menu-button {
  margin: 5px;
  font-size: large;
  color: whitesmoke;
}

.textarea-button {
  margin-top: 5px;
  margin-right: 5px;
  color: whitesmoke;
}

.key-input {
  font-size: medium;
  background-color: whitesmoke;
}

.ant-modal-header,
.ant-modal-footer {
  background-color: #333842;
  color: whitesmoke;
}

.ant-modal-title {
  color: whitesmoke;
}

.form-title {
  padding-top: 10px;
}

